.Navbar
{
    display: flex;
    justify-content: space-around;
    /* align-items: center; */
    padding: 1rem;
    position: fixed;
    top: 0;
    width: 100%;
    height: 90px;
}
.nav-menu
{
    display: flex;
}
.nav-menu li
{
  padding: 0 1rem;
}
.nav-menu li a
{
    color: blue;
    font-size: 1.3rem;
    font-weight: 600;
    text-transform: uppercase;
}
