*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Outfit', sans-serif;
}

ul li 
{
    list-style: none;
}
ul li a,a
{
    color: red;
    text-decoration: none;
}
body
{
    background: darkgray;
}
h1,
h4,
p
{
    color: #fff;
}
.btn
{
    padding: 12px 32px;
    font-size: 1rem;
    text-transform: uppercase;
    background: rgb(248, 217, 15);
    color: #222;
    border: 1px solid #fff;
    font-weight: 600;
    cursor: pointer;
}
.btn-light
{
    background: transparent;
    color: #fff;
}
.btn:hover
{
    background: rgba(255, 255, 255, 0.2);
    color: #fff;
    transition: 0.20s;
}
.Color
{
    color: #fff;
}
